// Importing Components
import Loader from '../ui/Loader'
import React, { useState } from 'react';

export default function Tasks({ todos, onDelete }) {


	return (
		<div>
				{todos === undefined ? <Loader  /> : todos.map((todo, key) => <Task onDelete={onDelete} className="text-left" key={key} todo={todo} />)}
		</div>
	)
}

function Task({ todo, onDelete }) {
	const [loading, setLoading] = useState(false)

	const handleDelete = () => {
		setLoading(true)
		onDelete(todo)
	}

	return (
		<div className="my-1 d-flex justify-content-between">
			<div>
				<input checked={todo.done} type="checkbox" className="checked:bg-blue-600 checked:border-transparent mr-2"/>
				{todo.title}
			</div>
			<button onClick={handleDelete} className="btn btn-danger btn-sm"> { loading ? <Spinner/> : <span className="font-bold"><i className="fa fa-times pr-1"></i>Delete</span>} </button>
		</div>
	)
}


const Spinner = () => {
	return (
		<div>
			<span class="spinner-border spinner-border-sm text-light" role="status" aria-hidden="true"></span> <span className="font-bold">Deleting</span>
		</div>
	)
} 