import './App.scss';
import React, { useState, useEffect } from 'react';

// Importing Components
import Tasks from './components/Tasks'
import Form from './components/Form'
import Loader from './ui/Loader'
const axios = require('axios');

function App() {

	const [todos, SetTodos] = useState([])
	const [loading, setLoading] = useState(true)

	// GET
	const getTodos = async () => {
		await axios.get('https://pau16-todolist-api.herokuapp.com/api/v1/todos', {
			method: "GET",
			headers: {
				"Access-Control-Allow-Origin": "*",
			},
		})
			.then(function (response) {
				// handle success
				setLoading(false)
				SetTodos(response.data);
			})
			.catch(function (error) {
				// handle error
				console.log(error);
			})
	}

	useEffect(() => {
		getTodos()
	}, [])

	// POST
	const handleSubmit = async (e) => {
		e.preventDefault()
		await axios({
			method: 'post',
			headers: {
				"Access-Control-Allow-Origin": "*",
			},
			url: 'https://pau16-todolist-api.herokuapp.com/api/v1/todos',
			data: {
				title: e.target.title.value,
				done: false
			}
		})
			.then(function (response) {
			// handle success add new task and render new state
				SetTodos([response.data, ...todos])
		})
			.catch(function (error) {
				// handle error
				console.log(error);
			});
		e.target.title.value = ""
	}

	//DELETE
	const handleDelete = async (todo) => {
		console.log(todo.id)
		await axios({
			method: 'delete',
			headers: {
				"Access-Control-Allow-Origin": "*",
			},
			url: 'https://pau16-todolist-api.herokuapp.com/api/v1/todos/' + todo.id,
		})
			.then(function (response) {
				// handle success add new task and render new state
				setLoading(true)
				SetTodos(todos.filter(t => t.id !== todo.id))
				console.log("do")
			})
			.catch(function (error) {
				// handle error
				console.log(error);
			});
		setLoading(false)
	}


	const handleAllTasks = () => {
		getTodos()
		SetTodos(todos)
	}

	const handleCompletedTasks = () => {
		axios.get('https://pau16-todolist-api.herokuapp.com/api/v1/todos', {
			method: "GET",
			headers: {
				"Access-Control-Allow-Origin": "*",
			},
		})
			.then(function (response) {
				// handle success
				setLoading(false)
				SetTodos(response.data.filter(t => t.done === true));
			})
			.catch(function (error) {
				// handle error
				console.log(error);
			})
	}

	const handleUncompletedTasks = () => {
		axios.get('https://pau16-todolist-api.herokuapp.com/api/v1/todos', {
			method: "GET",
			headers: {
				"Access-Control-Allow-Origin": "*",
			},
		})
			.then(function (response) {
				// handle success
				setLoading(false)
				SetTodos(response.data.filter(t => t.done === false));
			})
			.catch(function (error) {
				// handle error
				console.log(error);
			})
	}

  return (
		<div className="App bg-blue-200 h-screen pt-24 d-flex justify-center">
			<div className="w-2/4">
				<h1 className="font-serif font-black text-3xl pb-8">Task Manager</h1>	

				<div className="mb-3">
					<form className="input-group" onSubmit={handleSubmit}>
						<input name="title" type="text" className="form-control" placeholder="Add a new task" aria-label="new todo" aria-describedby="newTodoInput" />
						<button className="btn btn-primary btn-bg-blue-800" type="submit" id="button-addon2">Add</button>
					</form>
					<button onClick={handleAllTasks} className="btn btn-warning m-2 shadow-md">All Tasks</button>
					<button onClick={handleCompletedTasks} className="btn btn-warning m-2 shadow-md">Completed Tasks</button>
					<button onClick={handleUncompletedTasks} className="btn btn-warning m-2 shadow-md">Uncompleted Tasks</button>
				</div>


				<div className="p-3 shadow-md bg-white rounded-lg">
					{ loading ? <Loader /> : <Tasks onDelete={handleDelete} todos={todos} /> }
				</div>
		 	</div>
    </div>
  );
}

export default App;
